import React from 'react';
import { Link } from 'react-router-dom';
import './Home.css';
import './TokushimaOnbiToumei.png';

function Home() {
    return (
        <div className="burgar">
            <aside>
            <h1></h1>
            <h4>64</h4><h3></h3>
            </aside>
            {/*<img src="TokushimaOnbiToumei.png" alt="" width="300" height="300" border="0"/>*/}
            <div class="GaiyouText">
                <h4>
                201912<br/>
                65<br/>
                <br/>
                <br/>
                <br/>
                <br/>
                </h4>
            </div>
            <input type="checkbox" id="menu-toggle"/>
            <label id="trigger" for="menu-toggle"></label>
            <label id="burger" for="menu-toggle"></label>
            <ul id="menu">
            <li><Link to='/'>Home</Link></li>
            <li><Link to='/about'>About</Link></li>
            <li><Link to='/portfolio'>Portfolio</Link></li>
            <li><Link color="primary" Link to='/contact'>Contact</Link></li>
            </ul>
        </div>
    );
}

export default Home;




